.background {
    z-index: 10; 
    position: fixed;
    bottom: 0;
    right: 0;
    top: 0; 
    width: 250px; 
    opacity: 100%; 
    backdrop-filter: blur(2px);
    background: rgba(0, 0, 0, 0.3);
}

.nav {
    /* z-index: 20; */
    display: flex;
    flex-direction: column;
    gap: 45px;
    margin-top: 63px;
    padding-left: 70px;
    /* width: 300px; */
    font-family: 'Montserrat', sans-serif;
    font-size: 22px;
    font-style: normal;
    font-weight: 600;
    line-height: 27px;
    /* background-color: red; */
    color: green;
        /* border: 2px solid green; */

}

.nav a {
    color: black;
    text-decoration: none;
}

.nav a:hover {
    color: #0080ff;
}

.nav a.active {
    color: #0059b2;
    text-decoration: underline;
}
.close {
    position: absolute;
    height: 24px;
    width: 24px;
    right: 24px;
    top: 16px;

    background-color: transparent;
    border: none;
    cursor: pointer;
}

.close img {
    height: 24px;
    width: 24px;
}